// mobile-menu Olga Shapran
.menu-overlay {
    position: fixed;
    right: 20px;
    top: 50px;
    z-index: 1000;

    width: 130px;
    height: 255px;
    // !!!
    background-color: var(--heder-bcg-cl);

    box-shadow: 0px 4px 24px rgba(105, 105, 105, 0.1);
    border-radius: 30px 0px 30px 30px;

    opacity: 0;
    pointer-events: none;
    visibility: hidden;
    // !!!
    transition: opacity var(--anim), visibility var(--anim);

    @media screen and (min-width: 768px) {
        display: none;
    }

    &.is-open {
        opacity: 1;
        pointer-events: initial;
        visibility: visible;
    }
}

.menu {
    // display: flex;
    // flex-direction: column;
    // position: absolute;

    transform: translateX(100%);
    transition: transform var(--anim);

    @media screen and (min-width: 768px) {
        display: none;
    }

    .menu-overlay.is-open & {
        transform: translateX(0);
    }
}

.menu__btn {
    display: inline-flex;
    align-items: center;
    justify-content: center;

    position: fixed;
    right: 4px;
    top: 4px;
    padding: 0;
    margin-left: auto;

    background-color: transparent;
    border: none;
    cursor: pointer;
    // add
    fill: var(--primary-text-cl);

    box-shadow: inset 2px 1px 4px rgba(61, 61, 61, 0.1);

    .menu-overlay & {
        position: absolute;
    }
}

.menu__list{
   overflow: hidden;
}

.menu__item {
    padding-top: 16px;
    padding-bottom: 16px;
    padding-left: 16px;
    

    &:hover,
    &:focus,
    &:active {
        height: 100%;
        width: 100%;
        background-color: var(--menu-avtiv-border-cl);
        
    }
}

.menu__link {
    font-weight: 400;
    font-size: 16px;
    line-height: calc(19 / 16);
    text-align: center;
    // letter-spacing: 0.02em;
    color: #000000;
}